import groupBy from 'lodash/groupBy';
import mapValues from 'lodash/mapValues';
import sumBy from 'lodash/sumBy';
export default {
  props: { items: Array, default: [] },
  computed: {
    brands: function() {
      return mapValues(groupBy(this.items, ({ brand }) => brand || 'No Brand'), x =>
        sumBy(x, 'qty')
      );
    },
  },
};
